function getTheme() {
  let bgColor = "black"
  let textColor = "white"

  // Find what time it is
  let currentHour = new Date().getHours()

  // Late Night colors
  if (currentHour <= 7) {
    bgColor = "#09263E"
    textColor = "#C9B97E"
  }

  // Morning colors
  if (currentHour >= 8 || currentHour <= 12) {
    bgColor = "#fbedc0"
    textColor = "#20396E"
  }

  // Day colors
  if (currentHour >= 13 || currentHour <= 15) {
    bgColor = "#7BD6C0"
    textColor = "#5300c6"
  }

  // Night colors
  if (currentHour >= 16) {
    bgColor = "#efc9af"
    textColor = "#104c91"
  }

  return { bgColor, textColor }
}

export default getTheme
